import React from 'react'
import './style.css'
import {Container} from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Grid from '@material-ui/core/Grid';
import {withStyles} from '@material-ui/core/styles';

class Presentation extends React.Component{

    render(){
        const { classes } = this.props;
        return(
            <>
                {/* <Paper className={classes.paper}> */}
                <Grid item lg="12">
                    <Container maxWidth="lg" className={classes.root}>
                        <Grid container spacing={3}>
                            <Grid item xs={12} align="left">
                                <FormControl variant="outlined" className={classes.formControl}>
                                    <InputLabel id="demo-simple-select-outlined-label">Platform</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        name="platform"
                                        value={this.props.platform}
                                        onChange={this.props.update}
                                        label="Platform"
                                    >
                                        <MenuItem value="youtube">YouTube</MenuItem>
                                        <MenuItem value="twitch">Twitch</MenuItem>
                                        <MenuItem disabled>--- Coming Soon ---</MenuItem>
                                        <MenuItem value="facebook" disabled>Facebook Gaming</MenuItem>
                                        <MenuItem value="mixer" disabled>Mixer</MenuItem>
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item md={12} xs={12}>
                            {/* </Grid><Grid item md={8} xs={12}> */}
                                {this.props.player()}
                            </Grid>
                            
                            {/* <Grid item md={4} xs={12}>
                                {this.props.chat()}
                            </Grid> */}
                        </Grid>
                    </Container>
                </Grid>
                {/* </Paper>  */}
            </>
        )
    }
}

const styles = theme => ({
    root : {
        // padding:'5% 2%'
    },
    paper : {
        padding: theme.spacing(2),
        textAlign: 'center'
    }
});

export default withStyles(styles)(Presentation);