import React from 'react'
import Presentation from './presentation'

class Container extends React.Component{
    state = {
        youtube : "UCNLm0XtW8zWuzmhD5BqXagw",
        twitch : "developerswork",
        mixer : "",
        facebook : "",
        player : {
            height : "80vh",
            width : "100%",
            type : "youtube",
            twitch : {
                height : "800",
                width : "100%",
            }
        },
        chat : {
            height : "80%",
            width : "100%",
            url_restream : 'https://chat.restream.io/embed?token=bd031f04-e345-4529-ac76-6d73ec823c9a',
            url : 'https://streamlabs.com/widgets/chat-box/v1/FDA8853EE6C05819818F'
        }
    }  

    getChat = () => {
        return(
            <iframe
                src={this.state.chat.url}
                width={this.state.chat.width}
                style={{height:this.state.chat.height}}
            >
            </iframe>
        )
    }

    getPlayer = () => {
        switch(this.state.player.type){
            case "youtube":
                return this.getYouTubePlayer()
            case "twitch":
                return this.getTwitchPlayer()
        }
    }

    getYouTubePlayer = () => {
        return (
            <iframe 
                width={this.state.player.width}
                style={{height:this.state.player.height}}
                src={"https://www.youtube.com/embed/live_stream?channel=" + this.state.youtube} 
                frameborder="0" 
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                allowfullscreen>
            </iframe>
        )
    }

    getTwitchPlayer = () => {
        let script = document.createElement('script');
        script.src = 'https://embed.twitch.tv/embed/v1.js';
        script.addEventListener('load', () => {
            document.getElementById("twitch-embed").innerHTML = ""
            new window.Twitch.Embed("twitch-embed", {
                width: `${this.state.player.twitch.width}`,
                height: `${this.state.player.twitch.height}`,
                channel: `${this.state.twitch}`,
                // only needed if your site is also embedded on embed.example.com and othersite.example.com 
                parent: ["embed.example.com", "othersite.example.com"]
            });
        });
        document.body.appendChild(script);
        return (
            <>
                <div id="twitch-embed"></div>
            </>
        )
    }

    update = e => {
        switch(e.target.name){
            case "platform":
                this.setState({
                    player : {
                        ...this.state.player,
                        type : e.target.value
                    }
                });
                break
        }
    }

    render(){
        return (
            <Presentation
                update={this.update}
                player={this.getPlayer}
                chat={this.getChat}
                platform={this.state.player.type}
            />
        )
    }
}

export default Container